.features-page{max-width:1200px;margin:0 auto;padding:20px;background-color:#f0f4f8;border-radius:10px;box-shadow:0 0 20px rgba(0,0,0,.1);font-family:"Arial, sans-serif"}.features-header{text-align:center;padding:40px 20px;background:linear-gradient(to right, hsl(231, 62%, 42%), #3f41ca);color:#fff;border-radius:10px;margin-bottom:40px;box-shadow:0 4px 10px rgba(0,0,0,.1);transition:all .3s ease-in-out}.features-header:hover{transform:scale(1.05)}.features-header h1{font-size:3rem;margin-bottom:10px}.features-header p{font-size:1.2rem;margin-bottom:0}.feature-section{display:flex;flex-direction:column;align-items:center;margin-bottom:40px;padding:40px 20px;background:linear-gradient(to right, #f0f4f8, #ffffff);border-radius:10px;box-shadow:0 0 20px rgba(0,0,0,.05);transition:all .3s ease-in-out}.feature-section:hover{transform:translateY(-10px);box-shadow:0 0 30px rgba(0,0,0,.1)}.feature-section:nth-child(odd){background:linear-gradient(to right, #ffffff, #f0f4f8)}.feature-section h2{font-size:2rem;margin-bottom:20px;color:#333;border-bottom:2px solid #007bff;padding-bottom:10px}.feature-icon{font-size:4rem;color:#007bff;margin-bottom:20px;animation:iconBounce 2s infinite}@keyframes iconBounce{0%,20%,50%,80%,100%{transform:translateY(0)}40%{transform:translateY(-15px)}60%{transform:translateY(-10px)}}.feature-list{list-style:none;padding:0;width:100%;max-width:600px}.feature-list li{font-size:1.1rem;color:#555;margin-bottom:10px;padding:15px;background-color:#fff;border-radius:5px;box-shadow:0 0 10px rgba(0,0,0,.05);transition:all .3s ease-in-out;position:relative}.feature-list li:hover{transform:translateY(-5px);box-shadow:0 0 20px rgba(0,0,0,.1);background:linear-gradient(to right, #f0f4f8, #ffffff)}.feature-list li::before{content:"✓";color:#007bff;margin-right:10px;font-size:1.2rem}.feature-list li::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,.1);opacity:0;transition:opacity .3s ease-in-out}.feature-list li:hover::after{opacity:.1}